import Language from './Language'

class App extends Language {

  constructor() { super(); }

  /**
   * funes que necessitam ser carregadas na inicializao do app
   * 
   * this.navigatorLanguage()[Promise]["obtem linguagem padro do navegador e busca 
   *                                    labels com a linguagem configurada, se no
   *                                    tiver retorna o padro dos servidor que 
   *                                    pt-BR"];
   * this.generateKeys()     [Promise]["cria uma chave publica e privada para o app"]
   */
  async build() {
    await this.navigatorLanguage()
    await this.generateKeys()
    await this.sync({ build: true });

    this.window.onresize = (event) => {
      this.resize(event);
    };
  }
}

export default async function (Vue) {
  const app = new App()
  await app.build()
  Vue.config.globalProperties.$app = app
}